import React, { useEffect, useState } from "react";
import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getAllTravelEnquiries } from "../../redux/actions/enquiry";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";

const EnquiryTable = () => {
  const dispatch = useDispatch();
  const data =
    useSelector((state) => state.travelEnquiry.allTravelEnquiry) ?? [];

  useEffect(() => {
    dispatch(getAllTravelEnquiries());
  }, []);

  function handleSearch() {
    dispatch(
      getAllTravelEnquiries(
        startDate ? moment(startDate, "DD/MM/YYYY").format("DD/MM/YYYY") : null,
        endDate ? moment(endDate, "DD/MM/YYYY").format("DD/MM/YYYY") : null
      )
    );
  }

  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const handleDateChange = (date, name) => {
    if (name == "startDate") {
      setStartDate(date);
    } else if (name == "endDate") {
      setEndDate(date);
    }
  };

  return (
    <>
      <div className="container d-flex justify-content-around">
        <div className="row">
          <div className="col-md-0">
            <h3>Start Date:</h3>
          </div>
          <DatePicker
            selected={startDate}
            onChange={(e) => handleDateChange(e, "startDate")}
            dateFormat="dd/MM/yyyy" // You can customize the date format
            className="form-control" // Add the Bootstrap form-control class
          />

          <div className="col-md-0">
            <h3>End Date:</h3>
          </div>
          <DatePicker
            selected={endDate}
            onChange={(e) => handleDateChange(e, "endDate")}
            dateFormat="dd/MM/yyyy" // You can customize the date format
            className="form-control" // Add the Bootstrap form-control class
          />
        </div>

        {/* <DatePicker
          label="Start Date:"
          name={"startDate"}
          startDate={startDate}
          setStartDate={setStartDate}
        /> */}
        {/* <DatePicker
          name={"endDate"}
          label="End Date:"
          endDate={endDate}
          setEndDate={setEndDate}
        /> */}
        <div
          className="h-100 btn btn-primary btn-md ml-0 mt-0 mdb-color darken-2"
          role="button"
          tabIndex={0}
          onClick={() => handleSearch()}
        >
          Search
          <i class="fa fa-filter ml-2" />
        </div>
      </div>
      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th>ID</th>
            <th>Email</th>
            <th>Full Name</th>
            <th>Phone</th>
            <th>Destination</th>
            <th>Interests</th>
            <th>No. of Travelers</th>
            <th>Budget Per Person</th>
            <th>Special Notes</th>
            <th>Travel Month</th>
            <th>Trip Duration</th>
            <th>Created At</th>
          </tr>
        </thead>
        <tbody>
          {data &&
            data.map((enquiry) => (
              <tr key={enquiry.id}>
                <td>{enquiry.id}</td>
                <td>{enquiry.email}</td>
                <td>{enquiry.fullName}</td>
                <td>{enquiry.phone}</td>
                <td>{enquiry.destination.join(", ")}</td>
                <td>{enquiry.interests.join(", ")}</td>
                <td>{enquiry.noOfTravelers}</td>
                <td>{enquiry.budgetPerPerson}</td>
                <td>{enquiry.specialNotes}</td>
                <td>{enquiry.travelMonth}</td>
                <td>{enquiry.tripDuration}</td>
                <td>{new Date(enquiry.createdAt).toLocaleString()}</td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
};

export default EnquiryTable;
